import { Controller, Block } from '@ali/4ever-cangjie';
import { Heading } from '@ali/4ever-plugin-heading';
import { getSiblingDraggableBlocks } from '../utils/getDragNodes';
import { removeDragHightlightSingle, removeDragTableHightlightSingle, findDOMNodeByKey } from '../utils/domUtils';
import { DraggablePluginConfig } from '../types';
import { getClosestDom } from '../utils';

const { Heading1 } = Heading;

/**
 * 给已添加高亮可拖拽元素移除高亮（dom操作）
 * 不会引起 controller.value 变更
 */
export default function removeDragHighlightSingle(controller: Controller, payload) {
  const { key, isTableHighlight = false, type = 'hover' } = payload;
  const block = controller.value.document.getNode(key || '') as Block;

  if (!block) return controller;

  const blocks = getSiblingDraggableBlocks(controller, block);

  // 若支持议题拖拽，则议题的背景色需要统一
  const config = controller.query('getDraggableConfig') as DraggablePluginConfig;
  if (block?.data?.calloutPr ||
    (config && config.enableSubjectGroup && Heading1.isHeading1(block) && block.data.subjectPr)
  ) {
    const group = findDOMNodeByKey(`${block.key}-group`);
    if (group) {
      if (isTableHighlight) {
        removeDragTableHightlightSingle(group);
      } else {
        removeDragHightlightSingle(group);
      }
      return controller;
    }
  }
  const enableBorderHighlight = controller.query('enableBorderHighlight');

  blocks?.forEach((node) => {
    const dom = getClosestDom(node);
    if (isTableHighlight) {
      removeDragTableHightlightSingle(dom);
    } else {
      removeDragHightlightSingle(dom);
    }
  });

  if (blocks && enableBorderHighlight) {
    controller.dispatch('removeHighlight', {
      node: blocks,
      type,
      reason: 'dragBtn',
    });
  }

  return controller;
}
